.footer1{
width:  100%;
background: #333;
color:#888;
display: flex;
flex-direction: row;
justify-content: space-around;
height: auto;

}
.footer1 div{
    width: 20%
}
.footer1 h3{
color: white;
border-bottom: 1px solid #888;
padding-bottom: 5px;
margin-bottom: 30px
}
.footer1 i{
    width: 30px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    color: white;
    margin: 5px 7px
}
.footer1 i:nth-of-type(1){
    background: #3b5998
}
.footer1 i:nth-of-type(2){
    background: #55acee
}
.footer1 i:nth-of-type(3){
    background: #dd4b39
}
.footer1 i:nth-of-type(4){
    background: #ff0000
}
.footer1 i:nth-of-type(5){
    background: #007bb5
}

.footer1 ul li{
    padding: 10px 0px;
    list-style: none;
    color: #fff;
    border-bottom: 1px solid #888;
    margin-left: -40px
}
.footer1 button{
    padding: 5px 10px;
    background: #696969;
    border: 0;
    color: #f7f7f7;
    margin-left: 5px;
    margin-bottom: 5px
}
.footer1 img{
    width: 70px;
    height: 70px;
    border: 1px solid #888;
    padding: 1px
}
.copyright{
    background: #232323;
    color: white;
    width: 100%;
    height: 35px;
    display: flex;
    flex-direction: row;
    justify-content: space-around
}
.div1-copyright{
    width: 30%
}
.div2-copyright{
    width: 45%;
}
.ul-div2-copyright{
    display: flex;
    justify-content: space-between;
    list-style: none;
}
.p-div1-copyright .ul-div2-copyright{
    margin: 0px;
    
}
.p-div1-copyright i {
    color: #7cb62f
}
.ul-div2-copyright li{
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}
.ul-div2-copyright li:hover{
    color: #7cb62f;
    cursor: pointer;
}
.scrolltop{
    position: fixed;
    bottom: 10px;
    right: 10px;
    background: #7cb62f;
    color: #fff;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    cursor: pointer;
}
.scrolltop a{
    color: white;
} 
.div3-footer button a{
    text-decoration: none;
    color: white;
}
.div3-footer button a:hover{
    cursor: pointer;
    color: #7cb62f
}