.portfolio{
    display: flex;
    flex-direction: column;
    width: 100%;
    background: #f7f7f7
}
.ul-portfolio{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.portfolio-list{
    display: flex;
    flex-direction: row;
    list-style: none;
    text-align: center;
    margin: 20px 0
}
.portfolio-item{
    display: inline-block;
    width: 100px;
    padding: 10px;
}
.active{
    background: #7cb62f;
    color: #fff
}
.box{
    width: 100%;
    height: 500px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding-bottom: 25px
}
.boxdiv{
    width: 300px;
    font-size: 0;
    position: relative;
    overflow: hidden;
}

.img-box{
    width: 100%;
    height: 100%;
    margin: 15px 1px;
    border-radius: 5px;
}
.over{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(105, 206, 101, 0.5);
    font-size: 15px;
    opacity: 0;
    
}
.span-over{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background: #fff;
    display: block;
    padding: 10px 20px;
    cursor: pointer;
    border: 2px solid #7cb62f;   
}
.boxdiv:hover .over{
    opacity: 1;
}