@import url('https://fonts.googleapis.com/css?family=Lobster&display=swap');
@import url('https://fonts.googleapis.com/css?family=Raleway&display=swap');
.div1-articles span{
 display: flex;
 justify-content: center;
 align-items: center;
 width: 120px;
 height: 120px;
background-color: #7cb62f;
text-align: center;
border-radius: 50%;
color: white;
font-family: 'Lobster', cursive;
text-shadow: -2px -2px 0 rgba(0, 0, 0, 0.125);
text-transform: none;
font-size: 30px;
margin: auto;
}
.div1-articles:hover span{
    background: white;
    border: 2px solid #7cb62f;
    color: #7cb62f;
}
.div2-articles{
    background: #DEDEDE
}
.div1-div2-articles{
    background-color: #DEDEDE;
    width: 100%;
    height: auto
}
.h1-div1-div2-articles{
    display: flex;
    justify-content: center;
    padding-top: 20px;
    font-size: 60px;
    font-weight: 400;
    font-family: 'Lobster', cursive;
    line-height: 1;
    color: black
}
.s-div1-div2-articles{
    display: flex;
    justify-content: center;
    font-size: 30px;
    font-weight: 400;
    font-family: 'Lobster', cursive;
    line-height: 1;
    color: #888
}
.base-articles{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 100%;
    height: auto;
    padding: 20px 0px
}
.base{
    display: flex;
    flex-direction: column;
    width: 255px;
    height: 215px;
}
.img-choose{
    height: 200px;
    width: 200px;
    border-radius: 50%
}
.label-choose{
    font-size: 40px;
    font-weight: bold;
    font-family: 'Lobster', cursive;
    color: black;
    margin-left: 40px;
    transition: all 0.5s ease-in-out;
}
.label-choose:hover{
    color: #7cb62f;
    cursor: pointer;
}
.input-choose{
    width: 30px;
    height: 30px;
}
.input-choose:checked +label:before{
    background-color: #7cb62f;
    color: white;
}
