.button-submit{
    display: flex;
    justify-content: center;
    margin: auto;
    text-align: center;
    width: 200px;
    height: 200px;
    background-color: #7cb62f;
    color: white;
    border: 2px solid #7cb62f;
    border-radius: 50%;
    font-size: 25px;
    font-family:'Lobster', cursive;
    font-weight: 300;
   padding: 20px 0px;
}
.block{
    background: rgba(15, 15, 15, 0.582);
    height: 100vh ;
    width: 100% ;
    position: fixed;
    top: 0
}
.none{
    display: none;
}
.button-submit:hover{
background-color: white;
color: #7cb62f;
border: 2px solid #7cb62f;
cursor: pointer;
}
.span-blockkk{
    position: absolute;
    right: 20px;
    top:80px;
    font-size: 100px;
    color: white
}
.span-blockkk:hover{
    color: red;
    cursor: pointer;
}
.inputs-block{
    position: absolute;
    right: 150px;
    top:100px;
    padding: 20px;
    border: 2px solid white;
    height: 455px;
    border-radius: 5px;
    background: #5A5D70;
    opacity: 0.9
}
.inputsss-block{
    margin-bottom: 75px;
    width: 300px;
    height: 40px;
    border-radius: 5px;
    font-size: 20px;
    font-weight: 300;
    color: black
}
.inputsss-block:focus{
border:4px solid black
}
.choix-clientss{
    position: absolute;
    left: 100px;
    top:100px;
    width: 565px;
    height: 500px;
    border:2px solid white;
    border-radius: 5px;
    background: #5A5D70;
    opacity: 0.9;
    padding: 0px 15px;
    
}
.h1-choix-clientss{
    font-size: 30px;
    color: white;
    display: flex;
    justify-content: center;
    font-family: 'Lobster', cursive;
    border-bottom: 4px solid white
}
.choix-clientss p{
    font-size: 20px;
    font-weight: bold;
    background: white;
    color: #474444;
    font-family: 'Raleway', sans-serif; 
    margin-bottom: 30px;
    display: flex;
    justify-content: center;
    border: 2px solid white
}
.choix-clientss hr{
    border-top: 2px dashed white
}
.btn-submit-final{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    font-size: 20px;
    font-weight: bold;
    background: #7cb62f;
    color:white;
    border: none;
    border-radius: 5px;
    height: 30px;
    margin-top: -20px
}
.btn-submit-final:hover{
    background: white;
    color: #7cb62f;
    cursor: pointer;
    box-shadow: 0 5px 0 0 rgb(250, 248, 248);
}
.div-confirmation-final{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    background: white;
    width: 350px;
    height: 150px;
    text-align: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    border:4px solid #7cb62f;
    border-radius: 5px;
}
.div-confirmation-final p{
    font-size: 25px;
    font-weight: bold;
    color: #7cb62f;
}
.div-confirmation-final button{
    width: 60px;
    height: 25px;
    font-weight: bold;
}
.div-confirmation-final button:hover{
    cursor: pointer;
}
.div-confirmation-final{
   color:#7cb62f 
}
