.team{
    background: #f7f7f7;
    width: 100%;
    height: 630px;
}
.teaminfo{
    width: 100%;
    margin: auto;
    padding-top: 35px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
.div1-teaminfo{
    text-align: center;
    width: 20%;
}
.start-div1-teaminfo{
    display: flex;
    flex-direction: row;
    justify-content: center;
    color: #f2b100;
}
.img-div1-teaminfo{
    width: 100%;
    height: 50%;
}
.h4-div1-teaminfo{
    margin: 5px 0px;
}
.s-div1-teaminfo{
    display: block;
    margin-bottom: 5px;
}
.p-div1-teaminfo{
    background: #252525;
    padding: 20px;
    color: white;
    position: relative;
    display: flex;
    justify-content: space-between;
}
.p-div1-teaminfo i{
    position: relative;
    z-index: 15
}
.bg{
    display: block;
    width: 0;
    position: absolute;
    top: 0;
    left: 0;
    bottom:0;
    right: 0;
    transition: all 0.5s ease-in-out;
}
.div1-teaminfo:hover .bg{
    width: 100%;
    background: #7cb62f
}