.scroll{
width: 5px;
height: 10px;
margin: auto;
background: white;
position: relative;
top:4px;
-webkit-animation: scrolling 2s ease-in-out infinite;
        animation: scrolling 2s ease-in-out infinite;
}
@-webkit-keyframes scrolling {
    0%{
        opacity: 0;
        top:5px;
    }
    30%{
        opacity: 1;
        top:10px;
    }
    100%{
        opacity: 0;
        top:25px;
    }
}
@keyframes scrolling {
    0%{
        opacity: 0;
        top:5px;
    }
    30%{
        opacity: 1;
        top:10px;
    }
    100%{
        opacity: 0;
        top:25px;
    }
}
