@import url('https://fonts.googleapis.com/css?family=Lobster&display=swap');
body{
    margin: 0px;
    padding: 0px;
}
.section-singin{
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-top: 50px;
    height: 400px;
    width: 40%;
    align-items: center;
    margin-left: 30%;
    margin-top: 120px;
    margin-bottom: 500px
}
.sign-form{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin-top: 30px;
}
.signin-title{
    color:black;
    line-height: 1.1;
    font-family: 'Lobster', cursive;
    text-shadow: -2px -2px 0 rgba(0, 0, 0, 0.125);
    text-transform: none; 
    font-size: 4em;
}
.signin-input{
    width: 500px;
    height: 50px;
    margin-bottom: 30px;
    padding-left: 10px;
    color:#9ba0a3;
    border: 2px solid #c9cdcf;
    font-family: 'PT Sans', sans-serif;
    font-size: 20px;
    font-family: 'Times New Roman', Times, serif;
    font-weight: 300;
    color: black
}
.signin-input:focus{
    outline: none;
    border:  2px solid #7cb62f;
}
.signin-input:focus:invalid{
    background: url('https://pngimage.net/wp-content/uploads/2018/06/icone-croix-rouge-png-1.png') 98% center no-repeat    ;
    background-size: 15px
}
.signin-input:focus:valid{
    background: url('https://cdn3.iconfinder.com/data/icons/flat-actions-icons-9/512/Tick_Mark-512.png') 98% center no-repeat    ;
    background-size: 15px 
}


.sign-redirect{
    margin-bottom: 30px;
}
.sign-redirect-text{
    color:#333333;
    font-family: 'PT Sans', sans-serif;
}
.sign-redirect-link{
    color:#7cb62f;
    font-family: 'PT Sans', sans-serif; 
}
.sign-redirect-link:hover{
    text-decoration-line: none;
    cursor: pointer;
}
.sign-submit{
    height: 50px;
    background-color: #7cb62f;
    color: whitesmoke;
    border: none;
    font-size: 20px;
    font-weight: bold;
    width:150px;
    margin-top: 10px;
    border: 1px solid #7cb62f;
    border-radius: 5px;
}
.sign-submit{
    cursor: pointer;
}
.sign-submit:focus{
outline: none;
background-color: white;
color: #7cb62f;
border: 2px solid #7cb62f
}


