@import url('https://fonts.googleapis.com/css?family=Cairo&display=swap');
.contact{
    background-color: #F7F7F7
}
.div-contact{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 100%;
    height: 550px
}
.div1-contact{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 48%;
    margin-left: 50px
}
.p-div1-contact{
    width: 455px;
    margin-top: -40px
}
.h3-div1-contact{
    color:black;
    font-family:  'Cairo', sans-serif;
    font-size: 40px;
    font-weight: bold;

}
.p-div1-contact{
    color: #888;
    line-height: 1.6
}
.div1-contact span{
    display: block;
    margin-bottom: 10px;
    font-family:  'Cairo', sans-serif;
    font-size: 15px;
    font-weight: bold;
}
.div1-contact span i{
    border: 1px solid #7cb62f;
    width: 40px;
    height: 40px;
    text-align: center;
    transition: all 0.3s ease-in-out;
    line-height: 40px;
    border-radius: 50%;
    margin-right: 10px;
    color: #7cb62f
}
.div1-contact span:hover i{
    background: #7cb62f;
    color: #fff
}
.div2-contact{
    width: 48%;
    margin-top: 75px;
    display: flex;
    flex-direction: column;
    margin-right: 50px
}
.form-group{
    display: flex
}
.form-btn{
    background: #7cb62f;
    padding: 10px 20px;
    margin:10px auto;
    border: none;
    border-radius: 4px;
    font-size: 20px;
    color: #fff;
    display: block;
    outline: 0;
    float: right;
}
.form-btn:hover{
cursor: pointer;
box-shadow: 0px 15px 20px rgba(5, 236, 144, 0.4);
  color: #fff;
  transform: translateY(-5px);
}
.input{
    width: 100%;
    height: 40px;
    margin:10px 10px;
    border-radius: 5px
}
.input:focus{
    outline: none;
    border:  2px solid #7cb62f;
}
.input:focus:invalid{
    background: url('https://pngimage.net/wp-content/uploads/2018/06/icone-croix-rouge-png-1.png') 98% center no-repeat    ;
    background-size: 15px
}
.input:focus:valid{
    background: url('https://cdn3.iconfinder.com/data/icons/flat-actions-icons-9/512/Tick_Mark-512.png') 98% center no-repeat    ;
    background-size: 15px 
}
.textarea{
    width: 595px; 
    height: 235px;
    margin-left: 12px
}
.textarea:focus{
    outline: none;
    border:  2px solid #7cb62f;
}
