@import url('https://fonts.googleapis.com/css?family=Lobster&display=swap');
.divs{
  padding: 40px 0px
}
.overley{
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left:0;
  background: rgba(0,0,0,0.6)
}
.container{
  width: 85%;
  margin: auto
}
.div-tittle{
  font-size: 4em;
  display: flex;
  justify-content: center;
  line-height: 1.1;
  font-family: 'Lobster', cursive;
  text-shadow: -2px -2px 0 rgba(0, 0, 0, 0.125);
  text-transform: none;
  margin-bottom: 20px
}
