.about{
    background: #f7f7f7
}
.div-about{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 100%
}
.div-about-1{
    display: flex;
    flex-direction: column;
    width: 45%;
    margin-left: 50px
}
.img-div-about-2{
    width: 400px;
    height: 400px;
    border: 1px solid rgba(128, 128, 128, 0.493);
    padding: 15px 15px;
    border-radius: 5px;
    margin-top:30px;
    margin-right: 50px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.8)
}
.img-div-about-2:hover{
    cursor: pointer;
    opacity: .5
}
.h3-div-about-1{
    font-size:25px;
    font-weight: normal;
    margin-bottom: 20px;
}
.s-div-about-1{
    color: #7cb62f
}
.p-div-about-1{
    line-height: 1.8;
    color:#888;
    margin-bottom: 20px;
}
.span-div-about-1{
    border: 2px solid #7cb62f;
    border-radius: 5px;
    background-color: #7cb62f;
    width: 100px;
    height: 40px;
    color: white;
    text-align: center;
    align-items: center;
    justify-content: center;
    display: flex;
    padding: 5px 5px
}
.span-div-about-1:hover{
    cursor: pointer;
    box-shadow: 0 5px 0 0 rgb(250, 248, 248);
    opacity: .8
}