.services{
    background: #f7f7f7
}
.services1{
    height: auto;
    padding: 50px 0;
    overflow: hidden;
    background: #f7f7f7;
   
}
.services-card{
    margin-top: 20px;
    width: 30%;
    float: left;
    height: auto;
    padding: 50px 0;
    border: 1px solid #888;
    border-radius: 5px;
    box-sizing: border-box;
    text-align: center;
    margin-left: 5%;
    transition: all 0.6s ease-in-out
}

.first{
    margin-left: 0;
    background: white;
    transition: all 0.6s ease-in-out;
}
.first:hover{
    background: #7cb62f;
    cursor: pointer;
}
.first i{
    background: #7cb62f;
    width: 60px;
    height: 60px;
    text-align: center;
    line-height: 60px;
    color: #fff;
    border-radius: 50%;
    border : 2px solid #f6f6f6;
    transition: all 0.6s ease-in-out;
}
.first:hover i{
    background: #fff;
    color: #7cb62f;
}
.first h4{
    font-size: 25px;
    color: #7cb62f;
    margin-bottom: 60px;
    transition: all 0.6s ease-in-out;
}
.first:hover h4{
    color: white;
}
.line{
    width: 60%;
    margin: auto;
    margin-bottom: 20px;
}
.part-desc{
    font-size: 14px;
    color: #888;
    padding: 5px;
    transition: all 0.6s ease-in-out
}
.first:hover .part-desc{
    color: white;
}

