.field{
    background: #4c4c4c;
    height: 600px;
}
.field .div-tittle{
    color:white;
}
.accord{
    width: 90%;
    display: flex;
    height: 400px;
    overflow: hidden;
    margin: auto;
}
.accord li{
    width:18%;
    display: flex;
    justify-content: space-between;
    position: relative;
    float: left;
    transition: all 0.5s;
}
.title{
    background: rgba(0,0,0,0.5);
    position: absolute;
    top: 0;
    width: 640px;
}
.a-title{
    display: block;
    color: #fff;
    padding: 20px;
    font-size: 16px;
    text-decoration: none
}
.accord ul:hover li{
    width: 40px
}
.accord ul li:hover{
width: 640px
}
