@import url('https://fonts.googleapis.com/css?family=Lalezar&display=swap');
@import url('https://fonts.googleapis.com/css?family=Aguafina+Script&display=swap');
@import url('https://fonts.googleapis.com/css?family=Lalezar&display=swap');
.home{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 617px;
    text-align: center;
    background-image: url("https://static1.franprix.fr/articles/4/21/23/4/@/39271-salade-article_inside-2.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: relative;
    color: white;
}
.container1{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%)
}
.h3-container1{
    color:white;
    font-family: 'Lalezar', cursive;
    font-size: 55px;
    font-weight: 400;
    margin-bottom: 20px

}
.p-container1{
    font-family: 'Aguafina Script', cursive;
    font-size: 30px;
    font-weight: 400;
}
.btn-container1{
    background-color: #7cb62f;
    color:white;
    font-weight: 500;
    font-size: 25px;
    border: 2px solid transparent;
    border-radius: 5px;
    font-family: 'Lalezar', cursive;
    width: 210px;
    height: 68px;
    cursor: pointer;
    
}
.btn-container1:hover{
    box-shadow: 0 5px 0 0 rgb(250, 248, 248);
}
.singin{
    color: white;
    text-decoration: none
}
.navbar-footer{
    width: 40px;
    height: 53px;
    position: absolute;
    bottom: 10%;
    left: 49%;
    border: 2px solid white;
    border-radius: 18px;
}
.scroll{
width: 5px;
height: 10px;
margin: auto;
background: white;
position: relative;
top:4px;
animation: scrolling 2s ease-in-out infinite;
}
@keyframes scrolling {
    0%{
        opacity: 0;
        top:5px;
    }
    30%{
        opacity: 1;
        top:10px;
    }
    100%{
        opacity: 0;
        top:25px;
    }
}
@media(max-width:575px){
    .home{
        font-size: 25px
    }
}