@import url('https://fonts.googleapis.com/css?family=Lalezar&display=swap');
@import url('https://fonts.googleapis.com/css?family=Lobster&display=swap');

html{
    scroll-behavior: smooth;
}
.sticky {
    background: #fff;
    box-shadow: 0 2px 10px 0 #000;
    color: #000;
}
body{
    margin: 0px;
    padding: 0px;
}
.navbar{
    height: auto;
    position: fixed;
    width: 100%;
    z-index:999;
    background: black;
    opacity: .5;
    top:0
}
.container{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
.ul-list{
    display:flex;
    flex-direction: row;
    justify-content: space-around;
    width:  65%;
    list-style: none;
}
.logo{
    width: 50%;
    float: left;
}
.h2-logo{
    font-size: 30px;
    font-weight: bold;
    color: white;
   
}
.a-li{
    display: block;
    color: #222;
    text-decoration: none;
    padding: 10px 15px;
    font-weight: bold;
    color: white;
    
}
.a-li:hover{
    color: #7cb62f;
    cursor: pointer;
    
}

@media (max-width : 991px) {
    .logo {
        width: 100%;
        float: none;
    }
    .ul-list {
        width: 100%;
        float:  none;
        margin-top: 20px;
        display: none;
    }
    .list-item {
        display: block;
        text-align: center;
    }
}
.btn-articles{
    background-color: #7cb62f;
    color: white;
    width: 200px;
    border: 1px solid #7cb62f;
    border-radius: 5px;
    margin-left: 30px;
    font-size: 20px;
    font-weight: 300;
    line-height: 1.1;
  font-family: 'Lobster', cursive;
  text-shadow: -2px -2px 0 rgba(0, 0, 0, 0.125);
  text-transform: none;
}
.btn-articles:hover{
    background-color: white;
    color: #7cb62f;
}
